<template>
  <div class="circuit">
    <div class="circuit__cent"></div>
    <div class="circuit__right" v-show="type==='right'"></div>
    <div class="circuit__left" v-show="type==='left'"></div>
  </div>
</template>

<script>
export default {
  name: 'circuit',
  props: {
    type: String
  }
}
</script>

<style scoped>
.circuit{
  width: 347px;
  height: 447px;
  position: relative;
}
.circuit__cent{
  animation: Circuit 2s infinite;
  animation-direction:alternate;
  width: 100%;
  width: 347px;
  height: 447px;
  position: absolute;
  top: 0;
}
.circuit__left{
  width: 72px;
  height: 259px;
  position: absolute;
  bottom: -3px;
  left: -1.5%;
  background-image: url(../images/home/circuit__left.png);
}
.circuit__right{
  width: 92px;
  height: 338px;
  position: absolute;
  bottom: 30px;
  right: 28px;
  background-image: url(../images/home/circuit__right.png);
}
@keyframes Circuit{
  from{
    background:url(../images/home/circuit__switch--01.png);
  }
  to{
    background:url(../images/home/circuit__switch--02.png);
  }

}
</style>



