<template>
  <div class="home">
    <div class="home__nav">
      <div class="home__round__right">
        <round  @open='OpenRight' @close='CloseRight'>
          <router-link to="/MyTarget" tag="p" slot="title">自我评估</router-link>
          <img src="../images/home/round_line.gif" alt="" slot="round_cent">
        </round>
      </div>
      <div class="home__round__left">
        <round  @open='OpenLeft' @close='CloseLeft'>
          <router-link to="/input" tag="p" slot="title">名校展示</router-link>
          <img src="../images/home/round_school.png" alt="" slot="round_cent">
        </round>
      </div>
      <div class="home__circuit">
        <circuit :type='type'></circuit>
      </div>
      <div class="hme__gear">
        <gear :paused='paused'></gear>
      </div>
    </div>
    <div class="bottom">
      <div class="bottom__left">
        <div class="bottom__left__oricl"></div>
        <p> 北京箐英天下国际教育咨询有限公司</p>
      </div>
      <div class="bottom__right">
        <div class="bottom__right__oricl"></div>
        <p>技术支持：&nbsp北京赛亚数据科技有限公司</p>
      </div>
    </div>
  </div>

</template>

<script>
import round from '../components/round.vue'
import circuit from '../components/circuit.vue'
import gear from '../components/gear.vue'
export default {
  name: 'Home',
  components: {
    round,
    circuit,
    gear
  },
  data () {
    return {
      type: '',
      paused: false
    }
  },
  methods: {
    OpenRight () {
      this.type = 'left'
      this.paused = true
    },
    OpenLeft () {
      this.type = 'right'
      this.paused = true
    },
    CloseRight () {
      this.type = ''
      this.paused = false
    },
    CloseLeft () {
      this.type = ''
      this.paused = false
    }
  }
}
</script>

<style scoped>
.bottom{
  width:100%;
  height: 50px;
  position: fixed;
  bottom: 0;
}
.bottom__left{
  width:39.5%;
  position:absolute;
  bottom:0;
  left: -2%;
  height:40px;


}
.bottom__right{
  width:38%;
  height:40px;
  position:absolute;
  bottom: 0;
  right: -2%;
}
.bottom__left__oricl{
  height:40px;
  position: absolute;
  bottom:0;
  width:100%;
  border-top:2px solid rgb(177, 218, 217);
  border-right: 3px solid rgb(177, 218, 217) ;
  transform:  perspective(1em) rotateX(5deg);
}

.bottom__right__oricl{
  height:40px;
  position: absolute;
  bottom:0;
  width:100%;
  border-top:2px solid rgb(177, 218, 217);
  border-left: 3px solid rgb(177, 218, 217) ;
  transform:  perspective(1em) rotateX(5deg);
}
.bottom p{

  color:rgb(177, 218, 217);
  text-align: center;
  line-height:20px;
  font-size:12px;
  display:block;
}


.home__nav{
  width:780px;
  height:510px;
  margin: auto;
  position: fixed;
  bottom: 0;
  left: 0;
  right:0;
}
.home__round__right{
  position: absolute;
  left: 0;
}
.home__round__left{
  position: absolute;
  right: 0;
}
.home__circuit{
  position:absolute;
  bottom: 0;
  left: 226px;
  bottom: 70px;
}
.hme__gear{
  position:absolute;
  bottom: -130px;
  left: 269px;

}
</style>



