<template>
  <div class="round">
    <div class="round__outside">
      <img src="../images/component/round/round__outside.png" alt="" class="RotateAW" :class="{'paused': paused}">
    </div>
    <div class="round__inside">
      <img src="../images/component/round/round__inside.png" alt="" class="RotateCW" :class="{'paused': paused}">
    </div>
    <div class="round__cent">
      <slot name="round_cent"></slot>
    </div>
    <div class="round__title" @mouseover='open' @mouseout='close'>
      <slot name="title"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'round',
  data () {
    return {
      paused: false
    }
  },
  methods: {
    open () {
      this.paused = true
      this.$emit('open')
    },
    close () {
      this.paused = false
      this.$emit('close')
    }
  }
}
</script>

<style scoped>
.round{
  position: relative;
  width: 250px;
  height: 250px;
}
.round img{
  width:250px;
  height: 250px;
}
.round__outside{
  position: absolute;
  top: 0;

}
.round__inside{
  position: absolute;
  top: 0;
}
.round__cent{
  position: absolute;
  top: 0;
}

/*顺逆时针转动动画*/
.RotateAW{
  animation: RotateAW  5s linear infinite;
}
.RotateCW{
  animation: RotateCW  5s linear infinite;
}
.paused{
  animation-play-state:paused;
}
.round__title{
  width:125px;
  height: 125px;
  cursor:pointer;
  position:absolute;
  top:63px;
  left:63px;
  border-radius:50%;
}

.round__title:hover{
  background-color:rgba(10, 218, 217,0.3);
}
.round__title p{
  color:white;
  text-align: center;
  line-height:85px;
}
@keyframes RotateAW{
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes RotateCW{
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0);
  }
}
</style>



